// Arquivo: owlbear-rodeo-legacy/src/components/PlayerList.tsx

import { useState, useEffect } from "react";
import { Box, Text, Flex, IconButton } from "theme-ui";
import { X } from "react-feather";

function getCsrfToken(): string {
  const token = document.querySelector('meta[name="csrf-token"]')?.getAttribute('content');
  return token || '';
}

interface Player {
  id: number;
  username: string;
  nickname: string | null;
  avatar_url: string | null;
  is_online: boolean;
  is_master: boolean;
  last_seen: string | null;
}

interface PlayerListProps {
  sessionId: string;
  onClose?: () => void;
}

function PlayerList({ sessionId, onClose }: PlayerListProps) {
  const [players, setPlayers] = useState<Player[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // Buscar lista inicial de jogadores
  useEffect(() => {
    async function fetchPlayers() {
      try {
        const apiBase = (window as any).OWLBEAR_CONFIG?.apiBase || 'http://127.0.0.1:8000/api';
        const response = await fetch(`${apiBase}/owlbear/sessions/${sessionId}/players`, {
          credentials: 'include',
          headers: {
            'X-CSRF-TOKEN': getCsrfToken(),
            'Accept': 'application/json',
          },
        });

        if (!response.ok) {
          throw new Error('Falha ao buscar jogadores');
        }

        const data = await response.json();
        setPlayers(data.players || []);
        setLoading(false);
      } catch (err: any) {
        console.error('Erro ao buscar jogadores:', err);
        setError(err.message);
        setLoading(false);
      }
    }

    fetchPlayers();
  }, [sessionId]);

  // Integrar com Laravel Echo (Reverb)
  useEffect(() => {
    // Verificar se Pusher est disponvel
    if (typeof (window as any).Pusher === 'undefined') {
      console.warn('Pusher no est disponvel. Instalando...');
      
      // Carregar Pusher dinamicamente
      const script = document.createElement('script');
      script.src = 'https://js.pusher.com/8.2.0/pusher.min.js';
      script.onload = () => initializeEcho();
      document.head.appendChild(script);
      return;
    }

    initializeEcho();
  }, [sessionId]);

  function initializeEcho() {
    const Pusher = (window as any).Pusher;
    
    if (!Pusher) {
      console.error('Pusher no disponvel');
      return;
    }

    // Configurar Pusher/Echo
    const pusher = new Pusher('608ff52cf3db455155e6', {
      cluster: 'sa1',
      wsHost: 'localhost',
      wsPort: 6001,
      forceTLS: false,
      disableStats: true,
      enabledTransports: ['ws', 'wss'],
      authorizer: (channel: any) => {
        return {
          authorize: (socketId: string, callback: Function) => {
            fetch('http://127.0.0.1:8000/broadcasting/auth', {
              method: 'POST',
              headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'X-CSRF-TOKEN': getCsrfToken(), // CSRF ADICIONADO AQUI
              },
              credentials: 'include',
              body: JSON.stringify({
                socket_id: socketId,
                channel_name: channel.name,
              }),
            })
              .then(response => response.json())
              .then(data => callback(null, data))
              .catch(error => callback(error));
          },
        };
      },
    });

    // Conectar ao canal de presena da sesso
    const channel = pusher.subscribe(`presence-session.${sessionId}`);

    channel.bind('pusher:subscription_succeeded', (members: any) => {
      console.log(' Conectado ao canal de presena');
      console.log(' Membros online:', members);
      
      // Atualizar status de todos os membros online
      members.each((member: any) => {
        updatePlayerStatus(member.id, true);
      });
    });

    channel.bind('pusher:member_added', (member: any) => {
      console.log(' Jogador entrou:', member.info);
      updatePlayerStatus(member.id, true);
    });

    channel.bind('pusher:member_removed', (member: any) => {
      console.log(' Jogador saiu:', member.info);
      updatePlayerStatus(member.id, false);
    });

    // Eventos customizados
    channel.bind('player.joined', (data: any) => {
      console.log(' Player joined:', data);
      addOrUpdatePlayer(data.user, true);
    });

    channel.bind('player.left', (data: any) => {
      console.log(' Player left:', data);
      updatePlayerStatus(data.user.id, false);
    });

    // Cleanup ao desmontar
    return () => {
      pusher.unsubscribe(`presence-session.${sessionId}`);
    };
  }

  function updatePlayerStatus(userId: number, isOnline: boolean) {
    setPlayers(prev =>
      prev.map(p =>
        p.id === userId ? { ...p, is_online: isOnline } : p
      )
    );
  }

  function addOrUpdatePlayer(user: any, isOnline: boolean) {
    setPlayers(prev => {
      const exists = prev.find(p => p.id === user.id);
      if (exists) {
        return prev.map(p =>
          p.id === user.id ? { ...p, ...user, is_online: isOnline } : p
        );
      }
      return [...prev, { ...user, is_online: isOnline }];
    });
  }

  const onlinePlayers = players.filter(p => p.is_online);
  const offlinePlayers = players.filter(p => !p.is_online);

  if (loading) {
    return (
      <Box
        sx={{
          position: 'fixed',
          top: 80,
          left: 16,
          width: 280,
          maxHeight: 'calc(100vh - 100px)',
          backgroundColor: 'background',
          borderRadius: 8,
          boxShadow: '0 4px 12px rgba(0,0,0,0.15)',
          padding: 3,
          zIndex: 100,
        }}
      >
        <Text>Carregando jogadores...</Text>
      </Box>
    );
  }

  if (error) {
    return (
      <Box
        sx={{
          position: 'fixed',
          top: 80,
          left: 16,
          width: 280,
          backgroundColor: 'background',
          borderRadius: 8,
          boxShadow: '0 4px 12px rgba(0,0,0,0.15)',
          padding: 3,
          zIndex: 100,
        }}
      >
        <Text color="red">Erro: {error}</Text>
      </Box>
    );
  }

  return (
    <Box
      sx={{
        position: 'fixed',
        top: 80,
        left: 16,
        width: 280,
        maxHeight: 'calc(100vh - 100px)',
        backgroundColor: 'background',
        borderRadius: 8,
        boxShadow: '0 4px 12px rgba(0,0,0,0.15)',
        overflow: 'hidden',
        zIndex: 100,
      }}
    >
      {/* Header */}
      <Flex
        sx={{
          alignItems: 'center',
          justifyContent: 'space-between',
          padding: 3,
          borderBottom: '1px solid',
          borderColor: 'muted',
        }}
      >
        <Text variant="heading" sx={{ fontSize: 3 }}>
          Jogadores ({players.length})
        </Text>
        {onClose && (
          <IconButton onClick={onClose} sx={{ padding: 1 }}>
            <X size={20} />
          </IconButton>
        )}
      </Flex>

      {/* Player List */}
      <Box sx={{ overflowY: 'auto', maxHeight: 'calc(100vh - 200px)' }}>
        {/* Online Players */}
        {onlinePlayers.length > 0 && (
          <Box>
            <Text
              variant="heading"
              sx={{
                fontSize: 1,
                padding: 2,
                paddingBottom: 1,
                color: 'green',
                fontWeight: 'bold',
              }}
            >
               ONLINE ({onlinePlayers.length})
            </Text>
            {onlinePlayers.map(player => (
              <PlayerCard key={player.id} player={player} />
            ))}
          </Box>
        )}

        {/* Offline Players */}
        {offlinePlayers.length > 0 && (
          <Box>
            <Text
              variant="heading"
              sx={{
                fontSize: 1,
                padding: 2,
                paddingBottom: 1,
                color: 'muted',
                fontWeight: 'bold',
              }}
            >
               OFFLINE ({offlinePlayers.length})
            </Text>
            {offlinePlayers.map(player => (
              <PlayerCard key={player.id} player={player} />
            ))}
          </Box>
        )}
      </Box>
    </Box>
  );
}

function PlayerCard({ player }: { player: Player }) {
  return (
    <Flex
      sx={{
        alignItems: 'center',
        padding: 2,
        borderBottom: '1px solid',
        borderColor: 'muted',
        '&:hover': {
          backgroundColor: 'muted',
        },
      }}
    >
      {/* Avatar */}
      <Box
        sx={{
          width: 40,
          height: 40,
          borderRadius: '50%',
          backgroundColor: player.is_online ? 'green' : 'muted',
          marginRight: 2,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          color: 'white',
          fontWeight: 'bold',
          fontSize: 2,
        }}
      >
        {player.username.charAt(0).toUpperCase()}
      </Box>

      {/* Info */}
      <Box sx={{ flex: 1 }}>
        <Text
          sx={{
            fontWeight: 'bold',
            color: player.is_online ? 'text' : 'muted',
          }}
        >
          {player.username}
          {player.is_master && ' '}
        </Text>
        <Text
          variant="caption"
          sx={{
            color: player.is_online ? 'green' : 'muted',
            fontSize: 0,
          }}
        >
          {player.is_online ? 'Online' : 'Offline'}
        </Text>
      </Box>

      {/* Status Dot */}
      <Box
        sx={{
          width: 10,
          height: 10,
          borderRadius: '50%',
          backgroundColor: player.is_online ? 'green' : 'muted',
        }}
      />
    </Flex>
  );
}

export default PlayerList;