// owlbear-rodeo-legacy/src/components/party/Party.tsx
import { useState, useEffect } from "react";
import { Box, Flex, Text } from "theme-ui";
import { useParty } from "../../contexts/PartyContext";
import Nickname from "./Nickname";

type PartyProps = {
  gameId: string;
  stream: MediaStream | null;
  partyStreams: Record<string, MediaStream>;
  onStreamStart: (stream: MediaStream) => void;
  onStreamEnd: (stream: MediaStream) => void;
};

interface Player {
  id: number;
  username: string;
  nickname: string;
  avatar_url?: string;
  is_online: boolean;
  is_master: boolean;
  last_seen?: string;
  sessionId?: string;
  diceRolls?: any[]; // opcional
}

function Party({
  gameId,
  stream: _stream,
  partyStreams,
  onStreamStart: _onStreamStart,
  onStreamEnd: _onStreamEnd,
}: PartyProps) {
  // Referncia explcita para evitar TS6133 ("is declared but its value is never read")
  // No altera o comportamento  apenas marca como "usadas".
  void _stream;
  void _onStreamStart;
  void _onStreamEnd;

  const partyState = useParty();
  const [players, setPlayers] = useState<Player[]>([]);
  const [loading, setLoading] = useState(true);

  // Buscar lista inicial de jogadores
  useEffect(() => {
    const fetchPlayers = async () => {
      try {
        const response = await fetch(`/api/owlbear/sessions/${gameId}/players`, {
          credentials: "include",
        });

        if (response.ok) {
          const data = await response.json();
          setPlayers(data.players || []);
        }
      } catch (error) {
        console.error("Erro ao buscar jogadores:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchPlayers();
  }, [gameId]);

  // Conectar ao WebSocket e escutar eventos
  useEffect(() => {
    let channel: any = null;
    if ((window as any).Echo) {
      channel = (window as any).Echo.join(`session.${gameId}`);

      channel
        .here((users: any[]) => {
          setPlayers((prev) =>
            prev.map((p) => ({
              ...p,
              is_online: users.some((u) => u.id === p.id),
            }))
          );
        })
        .joining((user: any) => {
          setPlayers((prev) =>
            prev.map((p) => (p.id === user.id ? { ...p, is_online: true } : p))
          );
        })
        .leaving((user: any) => {
          setPlayers((prev) =>
            prev.map((p) => (p.id === user.id ? { ...p, is_online: false } : p))
          );
        })
        .listen(".player.joined", (event: any) => {
          setPlayers((prev) =>
            prev.map((p) =>
              p.id === event.user.id ? { ...p, is_online: true } : p
            )
          );
        })
        .listen(".player.left", (event: any) => {
          setPlayers((prev) =>
            prev.map((p) =>
              p.id === event.user.id ? { ...p, is_online: false } : p
            )
          );
        });
    }

    return () => {
      if (channel && (window as any).Echo) {
        (window as any).Echo.leave(`session.${gameId}`);
      }
    };
  }, [gameId]);

  // Chamar /join ao montar e /leave ao desmontar
  useEffect(() => {
    fetch(`/api/owlbear/sessions/${gameId}/join`, {
      method: "POST",
      credentials: "include",
    });

    return () => {
      fetch(`/api/owlbear/sessions/${gameId}/leave`, {
        method: "POST",
        credentials: "include",
      });
    };
  }, [gameId]);

  // Heartbeat a cada 30 segundos
  useEffect(() => {
    const interval = setInterval(() => {
      fetch(`/api/owlbear/sessions/${gameId}/heartbeat`, {
        method: "POST",
        credentials: "include",
      });
    }, 30000);

    return () => clearInterval(interval);
  }, [gameId]);

  const onlinePlayers = players.filter((p) => p.is_online);
  const offlinePlayers = players.filter((p) => !p.is_online);

  return (
    <Box
      sx={{
        width: "100%",
        height: "100%",
        overflow: "hidden",
        position: "relative",
      }}
    >
      <Flex
        sx={{
          flexDirection: "column",
          height: "100%",
          padding: 2,
        }}
      >
        <Text
          as="h2"
          variant="heading"
          sx={{
            marginBottom: 2,
            borderBottom: "1px solid",
            borderColor: "muted",
            paddingBottom: 1,
          }}
        >
          Party
        </Text>

        {/* Lista de jogadores do Owlbear (peer2peer) */}
        {Object.entries(partyState).map(([id, player]) => (
          <Nickname
            key={id}
            nickname={`${player.nickname}${
              id === player.sessionId ? " (you)" : ""
            }`}
            stream={partyStreams[id]}
            diceRolls={player.diceRolls ?? []}
          />
        ))}

        <Box sx={{ borderTop: "2px solid", borderColor: "primary", my: 2 }} />

        <Text
          as="h3"
          variant="body2"
          sx={{
            marginBottom: 1,
            fontWeight: "bold",
            color: "text",
          }}
        >
          Jogadores da Sesso
        </Text>

        {loading ? (
          <Text variant="body2" sx={{ color: "muted" }}>
            Carregando...
          </Text>
        ) : (
          <>
            {onlinePlayers.length > 0 && (
              <Box sx={{ marginBottom: 2 }}>
                <Text variant="caption" sx={{ color: "green", fontWeight: "bold" }}>
                   Online ({onlinePlayers.length})
                </Text>
                {onlinePlayers.map((player) => (
                  <Flex key={player.id} sx={{ alignItems: "center", marginY: 1 }}>
                    <Text variant="body2" sx={{ color: "green", marginLeft: 2 }}>
                      {player.username}
                      {player.is_master && " "}
                    </Text>
                  </Flex>
                ))}
              </Box>
            )}

            {offlinePlayers.length > 0 && (
              <Box>
                <Text variant="caption" sx={{ color: "gray", fontWeight: "bold" }}>
                   Offline ({offlinePlayers.length})
                </Text>
                {offlinePlayers.map((player) => (
                  <Flex key={player.id} sx={{ alignItems: "center", marginY: 1 }}>
                    <Text
                      variant="body2"
                      sx={{ color: "gray", marginLeft: 2, opacity: 0.6 }}
                    >
                      {player.username}
                      {player.is_master && " "}
                      {player.last_seen && ` (${player.last_seen})`}
                    </Text>
                  </Flex>
                ))}
              </Box>
            )}
          </>
        )}
      </Flex>
    </Box>
  );
}

export default Party;
